/* CommonCardStyles.css */

.card {
  width: 90%; /* Relative width to the parent container */
  max-width: 400px; /* Maximum width to ensure it doesn't get too large */
  height: auto; /* Let height adjust based on content */
  border: 1px solid #D0D0D0;
  border-radius: 4px;
  background-color: #FFFFFF;
  padding: 10px; /* Adjusted padding */
  position: relative;
  margin: 5px 0;
  box-sizing: border-box; /* Include padding and border in element's total width and height */
}

.label, .value, .location, .distance {
  font-family: 'Times New Roman', serif;
}

.label {
  font-size: 1rem; /* Responsive font size */
  color: #000;
  position: absolute;
  top: 10px;
  left: 15px;
}

.value {
  font-size: 1.5rem; /* Responsive font size */
  font-weight: bold;
  color: #000;
  position: absolute;
  bottom: 10px;
  left: 15px;
}

.location {
  font-size: 1.25rem; /* Responsive font size */
  font-weight: bold;
  color: #000;
  position: absolute;
  bottom: 10px;
  left: 15px;
}

.distance {
  font-size: 0.75rem; /* Responsive font size */
  color: #666;
  position: absolute;
  top: 10px;
  right: 15px;
}

/* Media Queries for iPhone and Other Mobile Devices */

@media (max-width: 768px) {
  .card {
      width: 95%; /* More width on smaller screens */
  }

  .label, .value, .location, .distance {
      font-size: 0.875rem; /* Slightly smaller font size on smaller screens */
  }
}
